<template>
    <div>
        <v-header>
            <div slot="right" :class="['h-right',name.length > 0 && items.length > 0 ? '' : 'opa-6']">
                <a @click="upload" class="mg-r10"></a>
            </div>
        </v-header>

        <v-content :type="'form'">
            <div class="section">
                <div class="matter">
                    <p class="title"></p>
                    <div class="edit" v-link="{name:'menu-all',query:{type:3,serviceId:serviceId,name:name}}">
                        <p class="text color-3" v-if="name.length > 0">{{name}}</p>
                        <p class="text color-9" v-else></p>

                        <span class="img transfer"></span>
                    </div>
                </div>
            </div>
            <div class="pic-area">
                <p class="title"></p>
                <div class="pic-view">
                    <div v-for="item in items"
                         style="width:{{picSize}}px;height:{{picSize}}px;background-image:url({{item.link}});"
                         :class="[$index % 4 == 0 ? 'first':'','picture']">
                        <span class="img del" @click="del($index)"></span>
                    </div>
                    <div :class="['add-pic',items.length % 4 == 0 ? 'first':'']"
                         style="width:{{picSize}}px;height:{{picSize}}px;">
                        <input type="file" accept="image/jpeg,image/png,image/jpg,image/gif"
                               class="upload opa-hide z-index-2" id="upload">
                        <span class="img add"></span>
                    </div>
                </div>
            </div>
        </v-content>
    </div>
</template>
<script>
    import VHeader from '../../components/Header'
    import VContent from '../../components/Content'
    import $ from 'jquery'
    import {st, url} from '../../const'
    require('../../assets/js/lrz.all.bundle');
    import {toast} from '../../assets/js/toast'

    export default{
        route: {
            data({from, to}){
                if (this.isReady) {
                    return {
                        state: !this.state,
                        isChoose: from.name == 'menu-all'
                    }
                }
            }
        },
        ready(){
            let vm = this;
            let size = $(window).width() - 20 - 5 * 3;
            vm.picSize = size / 4;
            st.set(st.keys.skillShow, {});
            $("#upload").click(function () {
                if (vm.items.length >= 4) {
                    toast.alert('');
                    return false;
                }
                $(this).val('');
            });
            $("#upload").change(function () {
                if (!this.files || !this.files.length) return;
                var files = this.files[0];
                var obj = this;
                if (!/image\/\w+/.test(files.type)) {
                    toast.alert("");
                    return false;
                } else {
                    var fileReader = new FileReader();
                    fileReader.onload = function (e) {
                        let ua = navigator.userAgent.toLowerCase();
                        if (/android/.test(ua)) {
                            toast.loadEnd();
                            var base64 = this.result;
                            vm.items.push({
                                link: base64,
                                file: files
                            });
                        } else {
                            lrz(files, {width: 667})
                                .then(function (rst) {
                                    toast.loadEnd();
                                    var base64 = rst.base64;
                                    vm.items.push({
                                        link: base64
                                    });
                                })
                                .catch(function (err) {
                                    toast.alert("");
                                });
                        }
                    };
                    fileReader.onerror = function (e) {
                        toast.alert("");
                    };
                    fileReader.readAsDataURL(files);
                    toast.loading();
                }
            });
            this.isReady = true;
        },
        data(){
            return {
                isReady: false,
                state: false,
                isChoose: false,

                picSize: 0,
                items: [],
                serviceId: 0,
                name: '',
                cuted: 0
            }
        },
        methods: {
            del(index){
                this.items.splice(index, 1);
            },
            upload(){
                if (this.name.length > 0 && this.items.length > 0) {
                    toast.loading();
                    let vm = this;
                    vm.cuted = 0;
                    let ua = navigator.userAgent.toLowerCase();
                    if (/android/.test(ua)) {
                        let items = vm.items;
                        setTimeout(function () {
                            for (let i in items) {
                                lrz(items[i].file, {width: 667})
                                    .then(function (rst) {
                                        vm.items[i].link = rst.base64;
                                        vm.cuted = vm.cuted + 1;
                                    });
                            }
                        }, 100);
                    } else {
                        this.$nextTick(function () {
                            vm.cuted = vm.items.length;
                        });
                    }
                }
            }
        },
        watch: {
            'state': function () {
                if (this.isChoose) {
                    let service = st.getJson(st.keys.skillShow);
                    if (service.serviceId) {
                        this.serviceId = service.serviceId;
                        this.name = service.name;
                    }
                } else {
                    this.serviceId = 0;
                    this.name = '';
                    this.items = [];
                    st.set(st.keys.skillShow, {});
                }
            },
            'cuted': function () {
                if (this.cuted == this.items.length) {
                    let picture = '';
                    let items = this.items;
                    for (let i = 0; i < items.length; i++) {
                        picture += (items[i].link + ' ');
                    }
                    console.log('itemsLen:', items.length, 'picture:', picture);
                    this.$http.get(url.newAddSkills, {
                        picture: picture,
                        type: this.serviceId
                    }).then(
                        ({data:{data, success, resultMsg}}) => {
                            if (success) {
                                toast.alert('');
                                window.location.href = "#/skill/show";
                            } else {
                                toast.alert(resultMsg);
                            }
                        })
                }
            }
        },
        components: {VHeader, VContent}
    }
</script>

<style scoped>
    .pic-area {
        margin-top: .1rem;
        padding: .15rem .1rem .25rem;
        background-color: #fff;
        overflow: hidden;
    }

    .pic-area .title {
        font-size: .16rem;
        margin-bottom: .1rem;
    }

    .pic-view > div {
        position: relative;
        margin-left: .05rem;
        margin-top: .1rem;
        float: left;
        background-size: 100% 100%;
    }

    .pic-view > div.first {
        margin-left: 0;
    }

    .pic-view > div:last-child {
        border: .01rem solid #f2f2f2;
    }

    .add-pic .upload {
        position: absolute;
        width: 100%;
        height: 100%;
    }

    .img.add {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-left: -.125rem;
        margin-top: -.125rem;
        width: .25rem;
        height: .25rem;
        background-size: .25rem;
        background-image: url(../../assets/img/icon/add_i01.png);
    }

    .img.del {
        position: absolute;
        right: -.03rem;
        top: -.03rem;
        width: .19rem;
        height: .19rem;
        background-size: .19rem;
        background-image: url(../../assets/img/icon/del02.png);
    }

    .add-pic .upload {
        position: absolute;
        width: 100%;
        height: 100%;
    }
</style>



